import { Component, OnInit, ViewEncapsulation, AfterViewInit, AfterViewChecked, ElementRef, ViewChild, HostBinding, Renderer2, Directive} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./ticket-dashboard.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ FLY_IN_OUT_ANIMATION, ROUND_ANTICLOCK_ANIMATION ]
})
export class TicketDashboardComponent implements OnInit {
	public ticketsfortoday = [];
	public closedticketfortoday = [];
	public opentickets = [];
	public queues = [];
	public traffic = [];
	public daysDetails = [];
	public weeksDetails = [];
	public monthsDetails = [];
	public quartersDetails = [];
	totalReportQueueOpen:string;
	
	constructor( private http: HttpClient, private router: Router, private route:ActivatedRoute ) {    }
	
	ngOnInit()
	{
		const headers = new HttpHeaders({'Content-Type':'application/json'});
	    this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/report-ticket-dashboard',{},
					{ headers, withCredentials: true }).subscribe(data => {
														this.daysDetails = data['day'];
														this.weeksDetails = data['week'];
														this.monthsDetails = data['month'];
														this.quartersDetails = data['quarter'];
														this.ticketsfortoday = data['populateTicketReportStatus'];
														this.closedticketfortoday = data['quarter'];
														this.opentickets = data['populateTotalOpenRecordStatus'];
														this.queues = data['populateTotalOpenRecordQueue'];
														this.totalReportQueueOpen = data['TotalReportQueueOpen'];
													});
		console.log(this.daysDetails.length);
		
	}
}


